import { useState, useEffect, useRef } from "react";
import Chart from "chart.js/auto";
import "./WeatherService.css"; //  

export default function WeatherService() {
    const [weatherData, setWeatherData] = useState(null);
    const [processedData, setProcessedData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [location, setLocation] = useState({ name: "  ()", nx: 60, ny: 127 });
    const [selectedDate, setSelectedDate] = useState(0); //  +0, +1, +2

    const [showChart, setShowChart] = useState(false); //   
    
    // Chart.js  ref
    const tempChartRef = useRef(null);
    const humidityChartRef = useRef(null);
    const tempChartInstance = useRef(null);
    const humidityChartInstance = useRef(null);

    // 2.    
    const createTemperatureChart = (data) => {
        if (tempChartInstance.current) {
            tempChartInstance.current.destroy();
        }

        const ctx = tempChartRef.current.getContext('2d');
        const filteredData = data.filter(item => item.date === formatDate(selectedDate));
        
        const labels = filteredData.map(item => formatDisplayTime(item.time));
        const temperatures = filteredData.map(item => parseInt(item.items.TMP) || 0);
        const precipitations = filteredData.map(item => parseInt(item.items.POP) || 0);

        tempChartInstance.current = new Chart(ctx, {
            type: 'line',
            data: {
                labels: labels,
                datasets: [
                    {
                        label: ' (C)',
                        data: temperatures,
                        borderColor: 'rgb(255, 99, 132)',
                        backgroundColor: 'rgba(255, 99, 132, 0.2)',
                        yAxisID: 'y',
                        tension: 0.4,
                        fill: false
                    },
                    {
                        label: ' (%)',
                        data: precipitations,
                        borderColor: 'rgb(54, 162, 235)',
                        backgroundColor: 'rgba(54, 162, 235, 0.2)',
                        yAxisID: 'y1',
                        tension: 0.4,
                        fill: false
                    }
                ]
            },
            options: {
                responsive: true,
                interaction: {
                    mode: 'index',
                    intersect: false,
                },
                plugins: {
                    title: {
                        display: true,
                        text: `${location.name} -    (${getDisplayDate(selectedDate)})`
                    },
                    legend: {
                        display: true,
                        position: 'top'
                    }
                },
                scales: {
                    x: {
                        display: true,
                        title: {
                            display: true,
                            text: ''
                        }
                    },
                    y: {
                        type: 'linear',
                        display: true,
                        position: 'left',
                        title: {
                            display: true,
                            text: ' (C)'
                        },
                        grid: {
                            drawOnChartArea: false,
                        },
                    },
                    y1: {
                        type: 'linear',
                        display: true,
                        position: 'right',
                        title: {
                            display: true,
                            text: ' (%)'
                        },
                        min: 0,
                        max: 100,
                        grid: {
                            drawOnChartArea: false,
                        },
                    }
                }
            }
        });
    };

    const createHumidityChart = (data) => {
        if (humidityChartInstance.current) {
            humidityChartInstance.current.destroy();
        }

        const ctx = humidityChartRef.current.getContext('2d');
        const filteredData = data.filter(item => item.date === formatDate(selectedDate));
        
        const labels = filteredData.map(item => formatDisplayTime(item.time));
        const humidity = filteredData.map(item => parseInt(item.items.REH) || 0);
        const windSpeed = filteredData.map(item => parseFloat(item.items.WSD) || 0);

        humidityChartInstance.current = new Chart(ctx, {
            type: 'bar',
            data: {
                labels: labels,
                datasets: [
                    {
                        label: ' (%)',
                        data: humidity,
                        backgroundColor: 'rgba(75, 192, 192, 0.6)',
                        borderColor: 'rgba(75, 192, 192, 1)',
                        borderWidth: 1,
                        yAxisID: 'y'
                    },
                    {
                        label: ' (m/s)',
                        data: windSpeed,
                        type: 'line',
                        borderColor: 'rgb(255, 205, 86)',
                        backgroundColor: 'rgba(255, 205, 86, 0.2)',
                        yAxisID: 'y1',
                        tension: 0.4,
                        fill: false
                    }
                ]
            },
            options: {
                responsive: true,
                plugins: {
                    title: {
                        display: true,
                        text: `${location.name} -    (${getDisplayDate(selectedDate)})`
                    },
                    legend: {
                        display: true,
                        position: 'top'
                    }
                },
                scales: {
                    x: {
                        display: true,
                        title: {
                            display: true,
                            text: ''
                        }
                    },
                    y: {
                        type: 'linear',
                        display: true,
                        position: 'left',
                        title: {
                            display: true,
                            text: ' (%)'
                        },
                        min: 0,
                        max: 100
                    },
                    y1: {
                        type: 'linear',
                        display: true,
                        position: 'right',
                        title: {
                            display: true,
                            text: ' (m/s)'
                        },
                        min: 0,
                        grid: {
                            drawOnChartArea: false,
                        },
                    }
                }
            }
        });
    };

    // 3.   useEffect 
    useEffect(() => {
        if (showChart && processedData && processedData.length > 0) {
            setTimeout(() => {
                createTemperatureChart(processedData);
                createHumidityChart(processedData);
            }, 100);
        }
        
        // cleanup 
        return () => {
            if (tempChartInstance.current) {
                tempChartInstance.current.destroy();
            }
            if (humidityChartInstance.current) {
                humidityChartInstance.current.destroy();
            }
        };
    }, [showChart, processedData, selectedDate, location]);

    //   
    const formatDate = (dateOffset = 0) => {
        const today = new Date();
        today.setDate(today.getDate() + dateOffset);
        const year = today.getFullYear();
        const month = String(today.getMonth() + 1).padStart(2, '0');
        const day = String(today.getDate()).padStart(2, '0');
        return `${year}${month}${day}`;
    };

    //     -    
    const formatTime = () => {
        const now = new Date();
        const availableTimes = ["0200", "0500", "0800", "1100", "1400", "1700", "2000", "2300"];
        const currentHour = now.getHours() * 100 + now.getMinutes(); // HHMM  
        
        //       
        for (let i = availableTimes.length - 1; i >= 0; i--) {
            if (parseInt(availableTimes[i]) <= currentHour) {
                return availableTimes[i];
            }
        }
        
        //        
        return "2300";
    };


    //    
    const getCategoryName = (category) => {
        const categories = {
            POP: "",
            PTY: "",
            REH: "",
            SKY: "",
            TMP: "",
            TMN: "",
            TMX: "",
            UUU: "",
            VVV: "",
            WAV: "",
            VEC: "",
            WSD: ""
        };
        return categories[category] || category;
    };

    //    
    const interpretWeather = (category, value) => {
        if (category === "SKY") {
            if (value === "1") return "";
            if (value === "3") return "";
            if (value === "4") return "";
        }
        if (category === "PTY") {
            if (value === "0") return "";
            if (value === "1") return "";
            if (value === "2") return "/";
            if (value === "3") return "";
            if (value === "4") return "";
        }
        return value;
    };

    //    
    // const getWeatherIcon = (sky, pty) => {
    //     if (pty !== "0") {
    //         if (pty === "1" || pty === "4") return "";
    //         if (pty === "2") return "";
    //         if (pty === "3") return "";
    //     } else {
    //         if (sky === "1") return "";
    //         if (sky === "3") return "";
    //         if (sky === "4") return "";
    //     }
    //     return "";
    // };

    const getWeatherIcon = (sky, pty) => {
        //    
        if (pty && pty !== "0") {
            if (pty === "1" || pty === "4") return "";
            if (pty === "2") return "";
            if (pty === "3") return "";
        }
        
        //     
        if (sky) {
            if (sky === "1") return "";
            if (sky === "3") return "";
            if (sky === "4") return "";
        }
        
        return ""; //  
    };

    useEffect(() => {
        async function fetchWeather() {
            setLoading(true);
            setError(null);
            
            // API    base_date base_time 
            const now = new Date();
            const currentTime = formatTime();
            let baseDate = formatDate(0);
            
            //    0200     
            if (now.getHours() < 2) {
                const yesterday = new Date(now);
                yesterday.setDate(yesterday.getDate() - 1);
                baseDate = yesterday.getFullYear() + 
                        String(yesterday.getMonth() + 1).padStart(2, '0') + 
                        String(yesterday.getDate()).padStart(2, '0');
            }
            
            console.log(`API  : =${baseDate}, =${currentTime}, nx=${location.nx}, ny=${location.ny},   =${selectedDate}`);

            // https://www.data.go.kr/data/15084084/openapi.do
            const url = `https://apis.data.go.kr/1360000/VilageFcstInfoService_2.0/getVilageFcst`;
            const params = new URLSearchParams({
                serviceKey: process.env.REACT_APP_API_KEY,
                pageNo: 1,
                numOfRows: 1000, //     (,   )
                dataType: "JSON",
                base_date: baseDate,
                base_time: currentTime,
                nx: location.nx,
                ny: location.ny
            });

            try {
                const response = await fetch(`${url}?${params}`);
                
                if (!response.ok) {
                    throw new Error(`HTTP : ${response.status} - ${response.statusText}`);
                }
                
                const data = await response.json();
                
                // API       
                if (data?.response?.header?.resultCode !== "00") {
                    throw new Error(`API : ${data?.response?.header?.resultMsg || '   '}`);
                }
                
                if (data?.response?.body?.items?.item) {
                    //    
                    const items = Array.isArray(data.response.body.items.item) 
                        ? data.response.body.items.item 
                        : [data.response.body.items.item];
                    
                    setWeatherData(items);
                    processWeatherData(items);
                } else {
                    throw new Error("    .");
                }
            } catch (err) {
                console.error("API  :", err);
                setError(`    : ${err.message}`);
            } finally {
                setLoading(false);
            }
        }

        fetchWeather();
    }, [location]); // selectedDate   -  

    //   
    const processWeatherData = (items) => {
        if (!items || !items.length) {
            setProcessedData([]);
            return;
        }

        //   
        const groupedData = {};
        
        items.forEach(item => {
            const key = `${item.fcstDate}-${item.fcstTime}`;
            if (!groupedData[key]) {
                groupedData[key] = {
                    date: item.fcstDate,
                    time: item.fcstTime,
                    items: {}
                };
            }
            groupedData[key].items[item.category] = item.fcstValue;
        });

        //       (  )
        const sortedData = Object.values(groupedData).sort((a, b) => {
            if (a.date === b.date) {
                return a.time.localeCompare(b.time);
            }
            return a.date.localeCompare(b.date);
        });

        setProcessedData(sortedData);
    };

    //    - //  
    const locationData = [
        { group: "", locations: [
            { name: "  ()", nx: 60, ny: 127 },
            { name: " ", nx: 61, ny: 126 },
            { name: " ", nx: 62, ny: 126 },
            { name: " ", nx: 61, ny: 128 },
            { name: " ", nx: 58, ny: 126 },
            { name: " ", nx: 59, ny: 125 },
            { name: " ", nx: 62, ny: 126 },
            { name: " ", nx: 61, ny: 129 },
            { name: " ", nx: 61, ny: 129 },
            { name: " ", nx: 61, ny: 127 },
            { name: " ", nx: 59, ny: 125 },
            { name: " ", nx: 59, ny: 127 },
            { name: " ", nx: 59, ny: 127 },
            { name: " ", nx: 61, ny: 125 },
            { name: " ", nx: 61, ny: 127 },
            { name: " ", nx: 61, ny: 127 },
            { name: " ", nx: 62, ny: 126 },
            { name: " ", nx: 58, ny: 126 },
            { name: " ", nx: 58, ny: 126 },
            { name: " ", nx: 60, ny: 126 },
            { name: " ", nx: 59, ny: 127 },
            { name: " ", nx: 60, ny: 127 },
            { name: " ", nx: 62, ny: 128 }
        ]},
        { group: "", locations: [
            { name: " ", nx: 97, ny: 74 },
            { name: " ", nx: 97, ny: 74 },
            { name: " ", nx: 98, ny: 75 },
            { name: " ", nx: 98, ny: 74 },
            { name: " ", nx: 97, ny: 75 },
            { name: " ", nx: 98, ny: 76 },
            { name: " ", nx: 98, ny: 75 },
            { name: " ", nx: 96, ny: 76 },
            { name: " ", nx: 99, ny: 75 },
            { name: " ", nx: 96, ny: 74 },
            { name: " ", nx: 98, ny: 77 },
            { name: " ", nx: 96, ny: 76 },
            { name: " ", nx: 98, ny: 76 },
            { name: " ", nx: 99, ny: 75 },
            { name: " ", nx: 96, ny: 75 }
        ]},
        { group: "", locations: [
            { name: " ", nx: 61, ny: 121 },
            { name: " ", nx: 63, ny: 124 },
            { name: " ", nx: 64, ny: 119 },
            { name: " ", nx: 59, ny: 123 },
            { name: " ", nx: 58, ny: 121 },
            { name: " ", nx: 57, ny: 128 },
            { name: " ", nx: 60, ny: 124 },
            { name: " ", nx: 58, ny: 125 },
            { name: " ", nx: 65, ny: 123 },
            { name: " ", nx: 62, ny: 127 },
            { name: " ", nx: 59, ny: 122 },
            { name: " ", nx: 55, ny: 128 },
            { name: " ", nx: 64, ny: 128 },
            { name: " ", nx: 61, ny: 134 },
            { name: " ", nx: 56, ny: 125 },
            { name: " ", nx: 57, ny: 123 },
            { name: " ", nx: 65, ny: 115 },
            { name: " ", nx: 61, ny: 131 },
            { name: " ", nx: 69, ny: 125 },
            { name: " ", nx: 71, ny: 121 },
            { name: " ", nx: 61, ny: 138 },
            { name: " ", nx: 62, ny: 118 },
            { name: " ", nx: 60, ny: 122 },
            { name: " ", nx: 61, ny: 130 },
            { name: " ", nx: 68, ny: 121 },
            { name: " ", nx: 56, ny: 131 },
            { name: " ", nx: 62, ny: 114 },
            { name: " ", nx: 64, ny: 134 },
            { name: " ", nx: 64, ny: 126 },
            { name: " ", nx: 57, ny: 119 }
        ]},
        { group: "", locations: [
            { name: " ", nx: 89, ny: 90 },
            { name: " ", nx: 90, ny: 91 },
            { name: " ", nx: 88, ny: 90 },
            { name: " ", nx: 89, ny: 90 },
            { name: " ", nx: 89, ny: 91 },
            { name: " ", nx: 89, ny: 90 },
            { name: " ", nx: 88, ny: 90 },
            { name: " ", nx: 86, ny: 88 }
        ]},
        { group: "", locations: [
            { name: " ", nx: 55, ny: 124 },
            { name: " ", nx: 55, ny: 124 },
            { name: " ", nx: 55, ny: 124 },
            { name: " ", nx: 55, ny: 123 },
            { name: " ", nx: 56, ny: 124 },
            { name: " ", nx: 55, ny: 125 },
            { name: " ", nx: 56, ny: 126 },
            { name: " ", nx: 55, ny: 126 },
            { name: " ", nx: 51, ny: 130 }
        ]},
        { group: "", locations: [
            { name: " ", nx: 53, ny: 38 },
            { name: " ", nx: 52, ny: 33 }
        ]}
    ];

    //   
    const handleLocationChange = (e) => {
        const selectedLocationName = e.target.value;
        
        //   
        for (const group of locationData) {
            const foundLocation = group.locations.find(loc => loc.name === selectedLocationName);
            if (foundLocation) {
                setLocation(foundLocation);
                break;
            }
        }
    };

    //   
    const handleDateChange = (offset) => {
        setSelectedDate(offset);
    };

    //    (YYYYMMDD -> YYYY.MM.DD)
    const formatDisplayDate = (dateString) => {
        if (!dateString) return "";
        return `${dateString.substring(0, 4)}.${dateString.substring(4, 6)}.${dateString.substring(6, 8)}`;
    };

    //    (HHMM -> HH:MM)
    const formatDisplayTime = (timeString) => {
        if (!timeString) return "";
        return `${timeString.substring(0, 2)}:${timeString.substring(2, 4)}`;
    };

    //   
    const getDayOfWeek = (dateString) => {
        if (!dateString) return "";
        const year = parseInt(dateString.substring(0, 4));
        const month = parseInt(dateString.substring(4, 6)) - 1; // JS  0 
        const day = parseInt(dateString.substring(6, 8));
        
        const date = new Date(year, month, day);
        const dayNames = ["", "", "", "", "", "", ""];
        return dayNames[date.getDay()];
    };

    //    (// + )
    const getDisplayDate = (offset) => {
        const dateString = formatDate(offset);
        const dayOfWeek = getDayOfWeek(dateString);
        let prefix = "";
        
        if (offset === 0) prefix = "";
        else if (offset === 1) prefix = "";
        else if (offset === 2) prefix = "";
        
        return `${prefix} (${formatDisplayDate(dateString)}, ${dayOfWeek})`;
    };

    if (error) return <div className="error-container">{error}</div>;

    return (
        <div className="weather-service">
            <h1> </h1>
            
            <div className="controls">
                <div className="location-selector">
                    <label htmlFor="location"> :</label>
                    <select 
                        id="location" 
                        value={location.name}
                        onChange={handleLocationChange}
                    >
                        {locationData.map((group) => (
                            <optgroup label={group.group} key={group.group}>
                                {group.locations.map((loc) => (
                                    <option value={loc.name} key={loc.name}>
                                        {loc.name}
                                    </option>
                                ))}
                            </optgroup>
                        ))}
                    </select>
                </div>
                
                <div className="selector-and-toggle-btn">
                    <div className="date-selector">
                        <button 
                            className={selectedDate === 0 ? 'active' : ''} 
                            onClick={() => handleDateChange(0)}
                        >
                            
                        </button>
                        <button 
                            className={selectedDate === 1 ? 'active' : ''} 
                            onClick={() => handleDateChange(1)}
                        >
                            
                        </button>
                        <button 
                            className={selectedDate === 2 ? 'active' : ''} 
                            onClick={() => handleDateChange(2)}
                        >
                            
                        </button>
                    </div>

                    <div className="chart-toggle">
                        <button 
                            className={showChart ? 'active chart-btn' : 'chart-btn'} 
                            onClick={() => setShowChart(!showChart)}
                        >
                            {showChart ? '  ' : ' '}
                        </button>
                    </div>
                </div>
            </div>

            {loading ? (
                <div className="loading">   ...</div>
            ) : processedData && processedData.length > 0 ? (
                <div className="weather-container">
                    <h2>{location.name}  {getDisplayDate(selectedDate)}</h2>

                    {/*  :   */}
                    {showChart && (
                        <div className="charts-container">
                            <div className="chart-wrapper">
                                <canvas ref={tempChartRef} id="temperatureChart"></canvas>
                            </div>
                            <div className="chart-wrapper">
                                <canvas ref={humidityChartRef} id="humidityChart"></canvas>
                            </div>
                        </div>
                    )}
                    
                    <div className="weather-cards">
                        {processedData
                            .filter(timeSlot => timeSlot.date === formatDate(selectedDate)) //  
                            .map((timeSlot, index) => {
                                const skyValue = timeSlot.items.SKY;
                                const ptyValue = timeSlot.items.PTY || "0"; //  
                                const tempValue = timeSlot.items.TMP;
                                const popValue = timeSlot.items.POP || "0"; //  
                                const rehValue = timeSlot.items.REH || "0"; //  
                                
                                //      
                                if (!skyValue || !tempValue) {
                                    return null;
                                }
                                
                                return (
                                    <div className="weather-card" key={`${timeSlot.date}-${timeSlot.time}`}>
                                        <div className="time">{formatDisplayTime(timeSlot.time)}</div>
                                        <div className="icon">{getWeatherIcon(skyValue, ptyValue)}</div>
                                        <div className="temp">{tempValue}C</div>
                                        <div className="condition">{interpretWeather('SKY', skyValue)}</div>
                                        <div className="details">
                                            <div>: {popValue}%</div>
                                            <div>: {rehValue}%</div>
                                        </div>
                                    </div>
                                );
                            })}
                    </div>
                </div>
            ) : (
                <div className="no-data">
                    {selectedDate > 0 ? 
                        `${selectedDate === 1 ? '' : ''}     .` : 
                        '  .'}
                </div>
            )}
        </div>
    );
}